.main {
    background-color: var(--color-grey-light-3);
    font-size: 1.5rem;
    padding: 2rem;

    @media only screen and (max-width: 56.25em) {
        padding: 1rem;
        font-size: 1.3rem;
    }
}
.search-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.list__card {
    padding: 2rem .1rem;
    &:not(:last-child) {
        margin-bottom: 1rem;
        border-bottom: .1rem solid var(--color-grey-dark-2);
    }

    display: grid;
    grid-template-columns: 1fr max-content;
    grid-gap: 1rem;
    align-items: center;
    
    // &-title {}
    &-icon {
        width: 2rem;
        height: 2rem;
        fill: currentColor;
    }
    &-extra {
        display: grid;
        grid-template-columns: min-content max-content;
        grid-gap: 1rem;
        align-items: center;

    }
    &-action {
        margin-top: 2rem;
        background-color: var(--color-primary-dark);
        border: none;
        border-radius: .5rem;
        padding: 2rem;
        justify-self: end;
        color: white;

        text-align: start;

        h3 {
            width: 100%;
            // padding: 3rem;
        }
        &-text {
            padding: 3rem;
            font-size: 1.3rem;
            display: none;

            &--actions:nth-last-child(4) {
                margin-bottom: 1rem;
            }
        }
    }
    @media only screen and (max-width: 37.5em) {
        grid-template-columns: auto;
    }
}
.charts {
    // padding: 3rem;
}
.title {
    padding-bottom: 3rem;
}
.barchart {
    width: 100%;

    &__items {
        display: flex;
        align-items: center;

        @media only screen and (max-width: 37.5em) {
            
        }
    }

    &__title {
        width: 10rem;
        margin-right: 1rem;
    }
    &__bar {
        padding: 1rem;
        margin: 1rem;
        grid-column: 2;
        color: rgba(255,255,255,1);
        background-color: var(--color-primary-light);
    }
}
.linechart, .scatchart, .doughchart {
    width: calc(100vw - 22rem);

    @media only screen and (max-width: 56.25em) {
        width: 90vw;
    }
}
.problem, .change {
    &:not(:last-child) {
        border-bottom: .1rem solid var(--color-grey-dark-2);
    }

    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
    grid-gap: 1rem;
    padding: 2rem .1rem;

    &__box {
        display: flex;
        flex-direction: column;
    }
    &__more {
        align-self: end;
        justify-self: end;
        border: none;
        background-color: transparent;
        cursor: pointer;

        display: grid;
        grid-template-columns: 1fr;
        justify-items: center;

        &-icon {
            width: 2.7rem;
            height: 2.7rem;
            fill: currentColor;
        }
    }
    &__extra {
        padding: 1rem;
        background-color: var(--color-primary-light);
        color: var(--color-grey-light-3);
        grid-column: 1/-1;
        display: none;
    }
}
[data-heading] {
    text-transform: uppercase;
}