.header {
    padding: 3rem;
    background-color: var(--color-primary-light);

    display: grid;
    grid-template-rows: 1fr min-content;
    justify-items: center;


    &__img {
        width: 100%;
    }
    &__title {
        font-size: 1.7rem;
        color: var(--color-off-white);
        text-transform: uppercase;
    }
    &__search {
        width: 100%;
        height: 6rem;
        background-color: var(--color-grey-light-3);
        padding: .7rem 2rem;
        font-family: inherit;
        font-size: 3rem;
        color: inherit;
        border: none;
        transition: all .2s;

        &:focus {
            outline: none;
        }
        &::-webkit-input-placeholder {
            font-weight: 100;
            color: var(--color-grey-dark-3);
        }
    }
    &__profile {
        padding: 2rem;
        justify-self: end;
        cursor: pointer;

        &-user {
            display: grid;
            grid-template-columns: repeat(2, max-content);
            grid-column-gap: 1rem;
            align-items: center;

            &-photo {
                height: 3.75rem;
                width: 3.75rem;
                border-radius: 50%;
            }
        }
        &:hover {
            background-color: var(--color-grey-light-3);
        }
    }
    @media only screen and (max-width: 56.25em) {
        display: none;
    }
}