.footer {
  background-color: white;
  padding: 3rem; }

.header {
  padding: 3rem;
  background-color: var(--color-primary-light);
  display: grid;
  grid-template-rows: 1fr min-content;
  justify-items: center; }
  .header__img {
    width: 100%; }
  .header__title {
    font-size: 1.7rem;
    color: var(--color-off-white);
    text-transform: uppercase; }
  .header__search {
    width: 100%;
    height: 6rem;
    background-color: var(--color-grey-light-3);
    padding: .7rem 2rem;
    font-family: inherit;
    font-size: 3rem;
    color: inherit;
    border: none;
    transition: all .2s; }
    .header__search:focus {
      outline: none; }
    .header__search::-webkit-input-placeholder {
      font-weight: 100;
      color: var(--color-grey-dark-3); }
  .header__profile {
    padding: 2rem;
    justify-self: end;
    cursor: pointer; }
    .header__profile-user {
      display: grid;
      grid-template-columns: repeat(2, max-content);
      grid-column-gap: 1rem;
      align-items: center; }
      .header__profile-user-photo {
        height: 3.75rem;
        width: 3.75rem;
        border-radius: 50%; }
    .header__profile:hover {
      background-color: var(--color-grey-light-3); }
  @media only screen and (max-width: 56.25em) {
    .header {
      display: none; } }

.main {
  background-color: var(--color-grey-light-3);
  font-size: 1.5rem;
  padding: 2rem; }
  @media only screen and (max-width: 56.25em) {
    .main {
      padding: 1rem;
      font-size: 1.3rem; } }

.search-box {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.list__card {
  padding: 2rem .1rem;
  display: grid;
  grid-template-columns: 1fr max-content;
  grid-gap: 1rem;
  align-items: center; }
  .list__card:not(:last-child) {
    margin-bottom: 1rem;
    border-bottom: 0.1rem solid var(--color-grey-dark-2); }
  .list__card-icon {
    width: 2rem;
    height: 2rem;
    fill: currentColor; }
  .list__card-extra {
    display: grid;
    grid-template-columns: min-content max-content;
    grid-gap: 1rem;
    align-items: center; }
  .list__card-action {
    margin-top: 2rem;
    background-color: var(--color-primary-dark);
    border: none;
    border-radius: .5rem;
    padding: 2rem;
    justify-self: end;
    color: white;
    text-align: start; }
    .list__card-action h3 {
      width: 100%; }
    .list__card-action-text {
      padding: 3rem;
      font-size: 1.3rem;
      display: none; }
      .list__card-action-text--actions:nth-last-child(4) {
        margin-bottom: 1rem; }
  @media only screen and (max-width: 37.5em) {
    .list__card {
      grid-template-columns: auto; } }

.title {
  padding-bottom: 3rem; }

.barchart {
  width: 100%; }
  .barchart__items {
    display: flex;
    align-items: center; }
  .barchart__title {
    width: 10rem;
    margin-right: 1rem; }
  .barchart__bar {
    padding: 1rem;
    margin: 1rem;
    grid-column: 2;
    color: white;
    background-color: var(--color-primary-light); }

.linechart, .scatchart, .doughchart {
  width: calc(100vw - 22rem); }
  @media only screen and (max-width: 56.25em) {
    .linechart, .scatchart, .doughchart {
      width: 90vw; } }

.problem, .change {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  grid-gap: 1rem;
  padding: 2rem .1rem; }
  .problem:not(:last-child), .change:not(:last-child) {
    border-bottom: 0.1rem solid var(--color-grey-dark-2); }
  .problem__box, .change__box {
    display: flex;
    flex-direction: column; }
  .problem__more, .change__more {
    align-self: end;
    justify-self: end;
    border: none;
    background-color: transparent;
    cursor: pointer;
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center; }
    .problem__more-icon, .change__more-icon {
      width: 2.7rem;
      height: 2.7rem;
      fill: currentColor; }
  .problem__extra, .change__extra {
    padding: 1rem;
    background-color: var(--color-primary-light);
    color: var(--color-grey-light-3);
    grid-column: 1/-1;
    display: none; }

[data-heading] {
  text-transform: uppercase; }

.sidebar {
  background-color: var(--color-grey-dark-1);
  grid-row: 2/-1; }
  .sidebar__item {
    color: var(--color-off-white);
    padding: 3rem;
    font-size: 1.4rem;
    text-transform: uppercase;
    display: grid;
    grid-template-columns: repeat(2, max-content);
    grid-column-gap: 1rem; }
    .sidebar__item:link, .sidebar__item:visited {
      text-decoration: none; }
    @media only screen and (max-width: 37.5em) {
      .sidebar__item-text {
        display: none; } }
    .sidebar__item:hover {
      background-color: var(--color-primary); }
    .sidebar__item-active {
      background-color: var(--color-primary-dark); }
    @media only screen and (max-width: 56.25em) {
      .sidebar__item {
        justify-content: center; } }
    @media only screen and (max-width: 37.5em) {
      .sidebar__item {
        padding: 1.5rem; } }
  .sidebar__icon {
    width: 1.75rem;
    height: 1.75rem;
    fill: currentColor; }
    @media only screen and (max-width: 37.5em) {
      .sidebar__icon {
        width: 3rem;
        height: 3rem; } }
  @media only screen and (max-width: 56.25em) {
    .sidebar {
      display: grid;
      grid-template-columns: repeat(4, 1fr); } }
  @media only screen and (max-width: 37.5em) {
    .sidebar {
      background-color: black;
      grid-row: 1/1; } }

:root {
  --color-primary: cadetblue;
  --color-primary-light: darkcyan;
  --color-primary-dark: darkgreen;
  --color-grey-light-1: #faf9f9;
  --color-grey-light-2: #f4f2f2;
  --color-grey-light-3: white;
  --color-grey-light-4: #ccc;
  --color-grey-dark-1: #333;
  --color-grey-dark-2: #777;
  --color-grey-dark-3: #999;
  --color-off-white: #fefefe;
  --shadow-dark: 0 2rem 6rem rgba($color: black, $alpha: .3);
  --shadow-light: 0 .1rem .1rem rgba($color: black, $alpha: .01);
  --line: .1rem solid var(--color-grey-light-2); }

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-size: 62.5%; }

body {
  line-height: 1.6;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif; }

.container {
  display: grid;
  grid-template-columns: 18rem 1fr; }
  @media only screen and (max-width: 56.25em) {
    .container {
      grid-template-columns: 1fr; } }

.loading {
  margin: auto;
  font-size: 6rem; }

.fade-enter {
  opacity: 0; }

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity .2s ease-out; }
